import TextField from "@material-ui/core/TextField";

// import ChainAniFace from "./ChainAniFace";

function ChainAniAdvanced(props) {
    const { id, timeScale, repetitions, faceChange, handleChange } = props;

    return (
        <>
            <div className="ChainAni-fields">
                <TextField
                    onChange={handleChange}
                    label="Speed"
                    size="small"
                    margin="dense"
                    variant="outlined"
                    inputProps={{
                        type: "number",
                        step: 0.25,
                        name: "timeScale",
                        id: id,
                    }}
                    value={timeScale}
                />
                <TextField
                    onChange={handleChange}
                    label="Repetitions"
                    size="small"
                    margin="dense"
                    variant="outlined"
                    inputProps={{
                        type: "number",
                        name: "repetitions",
                        id: id,
                    }}
                    value={repetitions}
                />
            </div>
            {/* <ChainAniFace faceChange={faceChange} /> */}
        </>
    );
}

export default ChainAniAdvanced;
